<template>
  <div class="course-group-discuss">
    <head-top title="小组讨论">
      <div slot="right" class="head-right">
        <van-icon class="head-operate" name="close" @click="toStop"></van-icon>
        <van-icon class="head-operate" name="comment-o" @click="toStart"></van-icon>
      </div>
    </head-top>

    <van-tabs v-model="selectIndex" animated swipeable @change="groupChanged" v-if="groupList && groupList.length>0">
      <van-tab v-for="(item, index) in groupList" :key="index">
        <div slot="title">
          {{item.groupName}}
        </div>

        <div class="sub_nav">
          <span>当前论题</span>
          <div>
            <span>
              点赞数：{{item.result.good || 0}}
            </span>
            <span style="padding-left: 1rem;">
              得分：{{item.result.score || 0}}
            </span>
          </div>
        </div>
        <div class="sub_module">
          <span v-if="item.subject">
              论题{{item.subject.index}}：
              <span v-if="item.subject.type === defaultType">{{item.subject.content}}</span>
              <img v-else alt="" :src="item.subject.content" :style="{ maxWidth: '92vw', maxHeight: '35vw' }"
                      @click="preview(item.subject.content)"/>
          </span>
          <div v-else style="text-align: center;"> 暂无论题</div>
        </div>

        <div class="sub_nav">
          <span>讨论内容</span>
        </div>
        <!-- 聊天信息 -->
        <div class="chat" ref="chat">
          <div v-if="item.discussList && item.discussList.length>0" class="chat_content">
            <div v-for="(sub, k) in item.discussList" :key="k" class="chat_item left_item">
              <div class="chat_icon">
                <img :src="defaultPhotoImg" alt=""/>
              </div>

              <div class="chat_news">
                <div class="chat_news_name">
                  {{sub.studentName}}
                  <span class="time">{{sub.createTime}}</span>
                  <span class="operate">
                      <van-stepper input-width="10vw" button-size="6vw" slot="default" v-model="sub.score" integer
                                   min="0" max="100"
                                   @change="scoreSet(item, sub)">
                      </van-stepper>
                      <span class="score">分</span>
                       <van-icon :name="sub.good===0 ? 'good-job-o' : 'good-job'"
                                 :class="{'check': sub.good >0}"
                                 @click="goodSet(item, sub)">
                      </van-icon>
                    </span>
                </div>
                <div class="chat_news_cont">
                  <!--                  <span> {{sub.content}}</span>-->
                  <span v-if="sub.type === defaultType">{{sub.content }}</span>
                  <img v-else :src="sub.content" alt @click="preview(sub.content)"/>
                  <div class="chat_news_arrow"></div>
                </div>
              </div>
            </div>
          </div>
          <r-lack-data v-else></r-lack-data>
          <!-- </div> -->
        </div>

      </van-tab>
    </van-tabs>
    <r-lack-data v-else></r-lack-data>

    <!-- 新建讨论-->
    <van-popup v-model="prepareVisible" position="bottom">
      <van-tabs v-model="subjectIndex">
        <van-tab>
          <div slot="title">
            <van-icon name="bars"/>
            备课题目
          </div>

          <div v-if="prepareList && prepareList.length>0" class="prepare-list">
            <div v-for="(item, index) in prepareList" :key="index">
              <div class="prepare-nav">{{'课题'+(index+1) +'：'+ item.title}}</div>
              <van-checkbox-group v-model="item.checked" direction="horizontal" class="sub_module">
                <van-checkbox shape="square" v-for="(sub, index) in groupOptions" :key="index" :name="sub.value"
                              :disabled="prepareCheckeds.indexOf(sub.value)>-1 && item.checked.indexOf(sub.value)===-1">
                  {{sub.text}}
                </van-checkbox>
              </van-checkbox-group>
            </div>
          </div>
          <r-lack-data v-else></r-lack-data>
          <van-button type="info" style="width: 100%;" :diasbled="loading" @click="prepareSubmit">开始讨论</van-button>
        </van-tab>

        <van-tab>

          <div slot="title">
            <van-icon name="photo-o"/>
            自定义论题
          </div>

          <div class="sub_nav">
            <span>论题类型：</span>
            <van-radio-group v-model="discussForm.type" direction="horizontal">
              <van-radio :name="1">文本</van-radio>
              <van-radio :name="2">图片</van-radio>
            </van-radio-group>
          </div>

          <van-field required label="讨论小组：" :error-message="discussFormErr.groupChecked">
            <template #input>
              <van-checkbox-group v-model="discussForm.groupChecked" direction="horizontal">
                <van-checkbox shape="square" v-for="(sub, index) in groupOptions" :key="index" :name="sub.value">
                  {{sub.text}}
                </van-checkbox>
              </van-checkbox-group>
            </template>
          </van-field>

          <van-field required v-show="discussForm.type === defaultType" v-model="discussForm.content" rows="2"
                     label="讨论课题："
                     type="textarea" maxlength="500" :error-message="discussFormErr.content"
                     placeholder="请输入课题" autofocus autosize show-word-limit
                     :rules="[{ required: true, message: '请填写讨论课题' }]">
          </van-field>

          <van-field required v-show="discussForm.type !== defaultType" name="uploader" label="上传图片："
                     :error-message="discussFormErr.imageID">
            <template #input>
              <van-image class="upload_image" :src="uploadImage" @click="toUpload">
                <template #error>
                  <van-icon name="photo-o" size="40"></van-icon>
                </template>
              </van-image>
              <!--              <van-uploader ref="upload" v-model="uploader" :max-count="1" :before-read="beforeRead"-->
              <!--                            :after-read="afterRead"/>-->
              <van-icon v-if="discussForm.imageID" name="cross" class="upload_drop" @click="dropUpload"></van-icon>
            </template>
          </van-field>

          <van-button type="info" style="width: 100%;" :diasbled="loading" @click="pictureSubmit">开始讨论</van-button>

        </van-tab>
      </van-tabs>
    </van-popup>

  </div>
</template>

<script>
import { Dialog, ImagePreview } from "vant";
import { ContentType, ModulePicture } from "@/utils/enum";
import defaultImg from "../../../../assets/boy_img.png";
import { formatDate } from "@/utils/formatData";
import { FuncType, sendToAPP } from "@/utils/interFlow";
import axios from "axios";

export default {
  name: "groupDiscussion",
  components: {},
  data () {
    return {
      groupList: [],
      defaultPhotoImg: defaultImg,
      selectIndex: 0,
      groupDiscussionList: [],
      studentList: [],

      prepareSelectIndex: -1,
      prepareList: [],
      prepareVisible: false,
      subjectIndex: '1',
      subject: null,
      defaultType: ContentType.Text,
      uploader: [],

      discussForm: {
        groupChecked: [],
        type: 1,
        content: "",
        imageID: ""
      },
      discussFormErr: {
        groupChecked: "",
        content: "",
        imageID: ""
      },
      loading: false,
      imagePreview: null
    };
  },
  computed: {
    groupOptions () {
      return this.groupList.map(r => {
        return { text: r.groupName, value: r.groupID }
      });
    },
    prepareCheckeds () {
      return this.prepareList.reduce((list, r) => {
        return list.concat(r.checked);
      }, []);
    },
    uploadImage () {
      return this.discussForm.imageID ? this.$gatewayHttp.resourse.picture(this.discussForm.imageID, ModulePicture.GroupSubject) : "";
    }
  },
  mounted () {
    this.$nextTick(() => {
      window.uploadImage = imgFile => {
        this.afterRead(imgFile);
      };
    });

    this.getGroupList();
  },
  methods: {
    getGroupList () {
      this.groupList = [];
      this.$gatewayHttp.group.getLastGroupList().then(res => {
        if (res.Result) {
          if (res.Data && res.Data.length > 0) {
            res.Data.forEach(item => {
              this.groupList.push({
                groupName: item["GroupName"],
                groupID: item["GroupID"],
                gatewayID: item["GatewayID"],
                groupingID: item["GroupingID"],
                groupMax: item["GroupNumber"],
                studentID: item["StudentID"],

                result: {
                  good: 0,
                  score: 0,
                  total: 0
                },
                subject: null,
                discussList: []
              });
            });

            this.groupChanged(0);
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    groupChanged (value) {
      this.selectIndex = value;
      this.getGroupDiscussInfo(this.groupList[value]);
      this.getGroupDiscussResult(this.groupList[value]);
    },
    getGroupDiscussInfo (group) {
      this.$gatewayHttp.groupDiscussion.getDataInfo({
        groupID: group.groupID
      }).then(res => {
        if (res.Result && res.Data) {
          group.subject = {
            id: res.Data["GSID"],
            index: res.Data["GSSort"],
            type: res.Data["GSForm"],
            content: res.Data["GSForm"] === this.defaultType ? res.Data["GSContent"] : this.$gatewayHttp.resourse.picture(res.Data["GSContent"], ModulePicture.GroupSubject),
            state: res.Data["State"]
          };
          this.getGroupDiscussList(group);
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    getGroupDiscussResult (group) {
      this.$gatewayHttp.groupDiscussion.getDataResult({
        groupID: group.groupID
      }).then(res => {
        if (res.Result) {
          group.result = {
            good: res["Good"],
            score: res["Score"],
            total: res["Total"]
          };
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    getGroupDiscussList (group) {
      group.discussList = [];
      this.$gatewayHttp.groupDiscussion.getDataList({
        gsID: group.subject.id
      }).then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas) {
            // res.Datas.sort((a, b) => {
            //   return a.Key["SubmitTime"] > b.Key["SubmitTime"] ? -1 : 1;
            // });
            res.Datas.forEach(item => {
              group.discussList.push({
                studentID: item.Key["StudentID"],
                studentName: item["StudentName"],
                id: item.Key["GDID"],
                type: item.Key["GDForm"],
                content: item.Key["GDForm"] === ContentType.Text ? item.Key["GDContent"] : this.$gatewayHttp.resourse.picture(item.Key["GDContent"], ModulePicture.GroupDiscuss),
                createTime: formatDate(item.Key["SubmitTime"], "hh:mm:ss"),
                good: item.Key["GDGood"] || 0,
                score: item.Key["GDScore"] || 0
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },

    preview (src) {
      this.imagePreview = ImagePreview({
        images: [src],
        showIndex: false,
        onClose () {
        }
      });
    },
    scoreSet (item, sub) {
      this.$gatewayHttp.groupDiscussion.postSetScore({
        gdid: sub.id,
        gdscore: sub.score
      }).then(res => {
        if (res.Result) {
          item.result.score = item.discussList.reduce((total, a) => {
            return total + parseInt(a.score);
          }, 0);
          // this.$toast("编辑成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("编辑失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    },
    goodSet (item, sub) {
      const good = Math.abs(sub.good - 1);
      this.$gatewayHttp.groupDiscussion.postSetGood({
        gdid: sub.id,
        gdgood: good
      }).then(res => {
        if (res.Result) {
          sub.good = good;
          item.result.good = item.discussList.reduce((total, a) => {
            return total + a.good;
          }, 0);
          // this.$toast("编辑成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("编辑失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    },

    toStop () {
      Dialog.confirm({
        title: '提示',
        message: '是否结束当前所有小组论题？'
      }).then(() => {
        const groupIDs = this.groupList.map(r => r.groupID).join(",");
        this.$gatewayHttp.groupDiscussion.postSetStop({
          groupID: groupIDs
        }).then(res => {
          if (res.Result) {
            this.$toast("结束讨论成功");
          } else if (res.Info) {
            this.$toast(res.Info);
          } else {
            this.$toast("结束讨论失败");
          }
        }).catch(() => {
          this.$toast("操作失败");
        });
      }).catch(() => {
        // on cancel
      });
    },
    toStart () {
      this.prepareSelectIndex = -1;
      this.uploader = [];
      this.subjectIndex = "1";
      this.discussForm = {
        groupChecked: [],
        type: 1,
        content: "",
        imageID: ""
      };
      this.discussFormErr = {
        groupChecked: "",
        content: "",
        imageID: ""
      };
      this.getPrepareDiscussList();
      this.prepareVisible = true;
    },
    getPrepareDiscussList () {
      this.prepareList = [];
      this.$mainHttp.prepareGroupSubject.dataList({
        CHID: this.$store.getters.classHourID
      }).then(res => {
        if (res.Result) {
          res.Datas.sort((a, b) => {
            return a["CreateTime"] > b["CreateTime"] ? -1 : 1;
          });

          res.Datas.forEach(item => {
            this.prepareList.push({
              title: item["PGSContent"],
              createTime: formatDate(item["CreateTime"], "yyyy-MM-dd"),
              other: item["PGSTime"],
              id: item["PGSID"],
              checked: []
            });
          })
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
        }
      }).catch(() => {
      });
    },
    prepareSubmit () {
      if (this.prepareCheckeds.length === 0) {
        this.$toast("请给讨论课题指定小组");
        return;
      }

      let requestAll = [];
      this.loading = true;
      this.prepareList.forEach(item => {
        if (item.checked.length > 0) {
          requestAll.push(this.$gatewayHttp.groupDiscussion.postSetStart({
            groupID: item.checked.join(","),
            gsContent: item.title,
            gsForm: this.defaultType
          }));
        }
      });

      if (requestAll.length > 0) {
        axios.all(requestAll).then(
          axios.spread((...resList) => {
            let failIndexs = [];
            resList.forEach((res, i) => {
              if (res.Result) {
              } else {
                failIndexs.push(i);
              }
            });

            if (failIndexs.length === 0) {
              this.$toast("新建讨论成功");
              this.groupChanged(this.selectIndex);
              this.prepareVisible = false;
            } else if (failIndexs.length === 1 && failIndexs.length < resList.length) {
              this.$toast("部分讨论未创建成功，请刷新重试");
              this.groupChanged(this.selectIndex);
              this.prepareVisible = false;
            } else {
              this.$toast("操作失败！");
            }

            this.loading = false;
          })
        ).catch(() => {
          this.$toast("操作失败！");
          this.loading = false;
        });
      } else {
      }
    },
    toUpload () {
      sendToAPP(FuncType.albumOpen, "");
    },
    afterRead (file) {
      console.log("接收到的base64：", file);
      // 超过20M
      if (file.length > 1024 * 1024 * 20) {
        this.$toast("请上传小于20M的图片");
        return false;
      }
      if (file) {
        let reqData = {
          funType: ModulePicture.GroupSubject,
          imgBaseString: file
        };
        this.$gatewayHttp.resourse.upload(reqData).then(res => {
          if (res.Result) {
            this.discussForm.imageID = res.Data;
          }
        }).catch(() => {
        });
      }
    },
    dropUpload () {
      this.discussForm.imageID = "";
    },
    // // 返回布尔值
    // beforeRead (file) {
    //   if (file.type !== 'image/jpeg') {
    //     this.$toast('请上传 jpg 格式图片');
    //     return false;
    //   }
    //   return true;
    // },
    // afterRead (file) {
    //   file.status = 'uploading';
    //   file.message = '上传中...';
    //
    //   setTimeout(() => {
    //     // file.status = 'failed';
    //     // file.message = '上传失败';
    //     file.status = 'success';
    //     file.message = '';
    //   }, 1000);
    // },
    pictureSubmit () {
      this.discussFormErr = {
        groupChecked: "",
        content: "",
        imageID: ""
      };
      if (this.discussForm.groupChecked.length <= 0) {
        this.$toast("请选择讨论小组!");
        this.discussFormErr.groupChecked = "请选择讨论小组";
        return
      }
      if (this.discussForm.type === this.defaultType && this.discussForm.content.length <= 0) {
        this.$toast("请填写课题内容!");
        this.discussFormErr.content = "请填写课题内容";
        return
      }
      if (this.discussForm.type !== this.defaultType && this.discussForm.imageID.length <= 0) {
        this.$toast("请上传课题图片!");
        this.discussFormErr.content = "请上传课题图片";
        return
      }
      this.loading = true;
      this.$gatewayHttp.groupDiscussion.postSetStart({
        groupID: this.discussForm.groupChecked.join(","),
        gsContent: this.discussForm.type === this.defaultType ? this.discussForm.content : this.discussForm.imageID,
        gsForm: this.discussForm.type
      }).then(res => {
        if (res.Result) {
          this.$toast("新建讨论成功");
          this.groupChanged(this.selectIndex);
          this.prepareVisible = false;
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("新建讨论失败");
        }
        this.loading = false;
      }).catch(() => {
        this.$toast("操作失败");
        this.loading = false;
      });
    }
  },
  beforeDestroy () {
    if (this.imagePreview) this.imagePreview.close();
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";
.course-group-discuss {
  min-height: 100%;
  background-color: #fff;

  .head-operate {
    padding-left: 15px;
    font-size: @font-title * 1.2;
    font-weight: 900;
    line-height: 44px;
  }

  .van-icon-close {
    color: #ff976a;
  }

  .sub_module {
    & img {
      text-align: center;
      max-width: 80vw;
      max-height: 60vw;
    }
  }

  .prepare-list {
    text-align: left;

    .prepare-nav {
      padding: 10px 16px;
      background-color: #f6f7fa;
      font-size: @font-sub-title;
      color: #111;
      line-height: 1.9;
    }
  }

  .upload_image {
    width: 80px;
    height: 80px;
  }

  .upload_drop {
    position: absolute;
    top: 0;
    left: 75px;
    font-size: @font-info * 0.6;
    padding: 2px;
    color: #fff;
    background: #969799;
    border-radius: 100%;
  }
}
</style>
